////////////////////
// Font
////////////////////
@import url(https://fonts.googleapis.com/earlyaccess/notosanstc.css);

////////////////////
// Golbel
////////////////////
html, body {
  margin: 0;
  padding: 0;
  @include size(100%);
  font-size: 16px;
  background-color: $lightColor;
  color: $darkColor;
  font-family: 'Montserrat', sans-serif, 'Noto Sans TC', 'Microsoft JhengHei'; }
body {
  overflow-x: hidden; }

////////////////////
// Text
////////////////////
h2 {
  font-size: 42px;
  font-weight: 600;
  margin-bottom: 16px;
  @include lg {
    font-size: 32px;
    margin-top: 32px;
    margin-bottom: 16px; } }

h3 {
  font-size: 18px;
  font-weight: 500;
  @include lg {
    font-size: 14px; } }

p {
  font-family: 'Noto Sans TC', 'Microsoft JhengHei';
  font-size: 18px;
  font-weight: 300;
  line-height: 1.75rem;
  margin-top: 0;
  margin-bottom: 24px;
  @include lg {
    font-size: 16px; } }

////////////////////
// Button
////////////////////
.btn {
  display: inline-block;
  vertical-align: middle;
  margin: 0;
  padding: 0 72px 0 16px;
  font-size: 14px;
  font-weight: 500;
  height: 44px;
  line-height: 44px;
  letter-spacing: 1px;
  background-color: $brandColor;
  color: $whiteColor;
  text-transform: uppercase;
  text-decoration: none;
  outline: none;
  cursor: pointer;
  overflow: hidden;
  user-select: none;
  -webkit-user-drag: none;
  span {
    display: inline-block;
    transition: all 0.3s cubic-bezier(0.2, 0, 0.2, 1);
    &:after {
      content: attr(data-hover);
      position: absolute;
      left: 0;
      top: 100%;
      width: 100%; } }
  &:hover {
    span {
      transform: translateY(-100%); } } }

////////////////////
// Hero
////////////////////
.hero {
  @include size(100%);
  z-index: 10;
  overflow: hidden;

  .portfolio {
    @include size(100%,100%);
    padding: 0 16.66667%;
    @include lg {
      overflow: auto; }
    .draw {
      height: 136px;
      width: auto;
      top: 50%;
      left: 0;
      transform: translateY(-60%);

      .st0 {
        fill: #2E2E2E; }
      .st1 {
        fill: #2456D8; }
      #P {
        opacity: 0;
        transition: all 1.2s cubic-bezier(0.2, 0, 0.2, 1);
        transition-delay: .2s;
        &:hover {
          .st0 {
            fill: #2456D8; }
          .st1 {
            fill: #2E2E2E; } } }
      #O {
        opacity: 0;
        transition: all 1.2s cubic-bezier(0.2, 0, 0.2, 1);
        transition-delay: .4s;
        &:hover {
          .st0 {
            fill: #2456D8; } } }
      #R {
        opacity: 0;
        transition: all 1.2s cubic-bezier(0.2, 0, 0.2, 1);
        transition-delay: .6s;
        &:hover {
          .st0 {
            fill: #2456D8; }
          .st1 {
            fill: #2E2E2E; } } }
      #T {
        opacity: 0;
        transition: all 1.2s cubic-bezier(0.2, 0, 0.2, 1);
        transition-delay: .9s;
        &:hover {
          .st0 {
            fill: #2456D8; }
          .st1 {
            fill: #2E2E2E; } } } } }

  header {
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    transition: all 0.3s;
    z-index: 15;
    @include lg {
      height: 48px;
      padding: 9px 20px; }

    .logo {
      position: absolute;
      display: block;
      top: 6.25vh;
      left: 6.25%;
      font-size: 0;
      @include lg {
        position: static; }

      img {
        height: 48px;
        padding: 9px 0;
        @include lg {
          height: 30px;
          padding: 0; } } }

    .resume {
      position: absolute;
      top: 6.25vh;
      right: 6.25%;
      @include size(10.4166666667%,48px);
      line-height: 48px;
      box-shadow: 0 6px 24px rgba(36,82,216,.25);
      @include lg {
        width: auto;
        top: 0;
        right: 0;
        padding: 0 20px; } } }

  .social {
    position: fixed;
    left: 6.25%;
    bottom: 6.25vh;
    font-size: 0;
    box-shadow: 0 2px 16px rgba(36,42,54,.1);
    background-color: $whiteColor;
    @include lg {
      left: 0;
      bottom: 0;
      width: 100%;
      box-shadow: none; }

    a {
      display: inline-block;
      @include size(48px);
      background-color: $whiteColor;
      color: $brandColor;
      font-size: 20px;
      line-height: 48px;
      text-align: center;
      transition: all 0.3s cubic-bezier(0.2, 0, 0.2, 1);
      overflow: hidden;
      &:before {
        content: "";
        position: absolute;
        left: 0;
        top: 0;
        width: 0;
        height: 100%;
        background-color: $brandColor;
        transition: all 0.3s cubic-bezier(0.2, 0, 0.2, 1); }
      &:hover {
        color: $whiteColor;
        &:before {
          width: 100%; } }

      i {
        line-height: 48px; } }

    .behance {
      border-right: 1px solid rgba(36,46,56,0.15); } }

  .scroll {
    position: fixed;
    right: 6.25%;
    bottom: 6.25vh;
    @include lg {
      right: 0;
      bottom: 0; }
    @include sm {
 }      //display: none

    .arrow {
      display: block;
      @include size(48px);
      background-color: $whiteColor;
      box-shadow: 0 0 10px rgba(36,42,54,.05);
      transition: all 0.3s cubic-bezier(0.2, 0, 0.2, 1);
      @include lg {
        @include size(44px); }
      &:after {
        content: "";
        display: blockblock;
        position: absolute;
        top: 50%;
        left: 50%;
        transform: translate(-50%,-50%);
        width: 0;
        height: 0;
        border-style: solid;
        border-width: 9px 6px 0 6px;
        border-color: $darkColor transparent transparent transparent;
        transition: all 0.3s cubic-bezier(0.2, 0, 0.2, 1); }
      &.arrow-top {
        &:after {
          transform: translate(-50%,-50%) rotate(180deg); } }
      &:hover {
        box-shadow: 0 8px 24px rgba(36,42,54,.15); } } }

  .copyright {
    position: fixed;
    right: 6.25%;
    bottom: 6.25vh;
    height: 48px;
    line-height: 48px;
    padding: 0 20px;
    background-color: $whiteColor;
    box-shadow: 0 2px 16px rgba(36,42,54,.1);
    @include lg {
      right: 0;
      bottom: 0; }
    span {
      &:after {
        content: '2018 © "JL CHEN" PORTFOLIO';
        font-size: 14px;
        font-weight: 600;
        color: $darkColor; }
      @include lg {
        &:after {
          content: '2018 © "JL CHEN"'; } } } } }

.show {
  .hero {
    .portfolio {
      .draw {
        #P, #O, #R, #T {
          opacity: 1; } } } } }

////////////////////
// Title
////////////////////
.title-section {
  .title {
    position: absolute;
    top: -120px;
    left: 6.25%;
    font-size: 32px;
    margin: 0;
    z-index: 2;
    @include lg {
      top: -16px;
      left: 0;
      font-size: 24px; }
    span {
      display: block;
      float: left;
      clear: both;
      padding: 2px 8px;
      background-color: $brandColor;
      color: #fff;
      &:first-child {
        margin-bottom: 4px;
        font-weight: 600; }
      &:last-child {
        font-weight: 500; } } } }

////////////////////
// About
////////////////////
.about-section {
  width: 100%;
  margin-bottom: 50vh;
  padding: 25vh 0 0;
  @include lg {
    margin-bottom: 120px;
    padding: 25vh 20px 0; }


  .about-wrapper {
    display: flex;
    @include lg {
      flex-direction: column; }

    .about-img-wrapper {
      @include size(40%,50vh);
      background-color: $darkColor;
      background-image: url("../images/photo.jpg");
      background-size: cover;
      background-position: center center;
      overflow: hidden;
      @include lg {
        width: 100vw;
        height: 75vw;
        margin-left: -20px; } }

    .about-info {
      width: 60%;
      padding: 0 16.66667% 0 10%;
      color: $darkColor;
      text-align: left;
      @include lg {
        width: 100%;
        margin-top: 20px;
        padding: 0; }
      h2 {
        margin-bottom: 6.66667vh; }
      h3 {
        position: absolute;
        right: 0;
        bottom: 0;
        @include lg {
          display: none; } }

      .intro {
        p {
          &:last-child {
            margin-bottom: 0; } } } } } }

////////////////////
// Works
////////////////////
.work-section {
  margin-bottom: 50vh;
  @include lg {
    margin-bottom: 120px;
    padding: 0 20px; }

  .work-wrapper {
    display: flex;
    margin-bottom: 25vh;
    @include lg {
      margin-bottom: 60px; }

    &:nth-child(odd) {
      flex-direction: row;
      padding: 0 12.5%;
      @include lg {
        flex-direction: column;
        padding: 0; }

      .work-info {
        padding-left: 10%;
        text-align: left;
        @include lg {
          width: 100%;
          padding-right: 0;
          padding-left: 0;
          text-align: left; }

        h3 {
          position: absolute;
          right: 0;
          bottom: 0;
          @include lg {
            right: auto;
            right: 0; } } } }

    &:nth-child(even) {
      flex-direction: row-reverse;
      padding: 0 12.5%;
      @include lg {
        flex-direction: column;
        padding: 0; }

      .work-info {
        padding-right: 10%;
        text-align: right;
        @include lg {
          width: 100%;
          padding-right: 0;
          padding-left: 0;
          text-align: left; }

        h3 {
          position: absolute;
          bottom: 0;
          left: 0; } } }

    .work-img-wrapper {
      @include size(50%,50vh);
      background-color: $darkColor;
      overflow: hidden;
      @include lg {
        width: 100vw;
        height: 75vw;
        margin-left: -20px; }

      .work-img {
        @include size(100%);
        background-size: cover;
        background-position: center center;
        transition: all 0.3s cubic-bezier(0.2, 0, 0.2, 1); }

      &:hover {
        .work-img {
          transform: scale(1.1); } } }

    .work-info {
      width: 50%;
      padding-top: 8.33333vh;
      color: $darkColor;
      @include lg {
        padding-top: 0; }

      >span {
        display: block;
        margin-bottom: 24px;
        color: #889098;
        font-size: 14px; } } } }

////////////////////
// Contact
////////////////////
.contact-section {
  padding: 0 20px; }
